/* ==========================================================================
   Header main menu style - begin
   ========================================================================== */

.eltd-main-menu {
    @include eltdRelativeHolderLayout();
    height: 100%;
    width: auto;
    vertical-align: middle;

    ul {
        list-style: none outside none;
        margin: 0;
        padding: 0;
        text-align: left;

        li {
            position: relative;
            display: inline-block;
            float: left;
            padding: 0;
            margin: 0;

            a {
                font-size: 14px;
                line-height: 16px;
                color: $default-menu-color;

                &:hover {
                    color: $default-menu-color;
                }

                i:before {
                    margin: 0 7px 0 0;
                }

                .menu_icon_wrapper {
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;
                }

                .eltd-menu-featured-icon {
                    position: relative;
                    top: -4px;
                    left: 5px;
                    color: currentColor;
                    font-size: 10px;
                    margin: 0;

                    &:before {
                        margin: 0;
                    }
                }
            }
        }
    }

    > ul {
        @include eltdRelativeHolderLayout();
        height: 100%;

        > li {
            float: left;
            height: 100%;
            box-sizing: border-box;

            > a {
                @include eltdRelativeHolderLayout();
                height: 100%;
                padding: 0 16px;
                color: $default-menu-color;
                font-size: 12px;
                line-height: 23px;
                text-transform: uppercase;
                letter-spacing: 2.5px;
                font-weight: 400;
                box-sizing: border-box;

                > span.item_outer {
                    position: relative;
                    display: block;
                    top: 50%;
                    @include eltdTransform(translateY(-50%));

                    .item_text {
                        position: relative;
                        display: inline-block;
                        vertical-align: middle;
                        box-sizing: border-box;

                        &:after {
                            content:"";
                            position:absolute;
                            bottom:0;
                            left:0;
                            width:calc(100% - 2px);
                            height:1px;
                            background-color:$default-menu-color;
                            @include eltdTransition(all .2s cubic-bezier(0.32, 0.93, 0.49, 0.77) 0.05s);
                            @include eltdTransform(scale(0,1));
                        }

                        .eltd-light-header .eltd-menu-area  &:after {
                            background-color:#ffffff;
                        }
                    }
                }
            }

            &.eltd-active-item > a,
            &:hover > a {
                span.item_outer .item_text {

                    &:after { 
                        @include eltdTransform(scale(1,1));
                    }
                }
            }

            &.has_sub {

                > a .eltd-menu-arrow {
                    display: none;
                }
            }
        }
    }
}

.eltd-light-header .eltd-page-header > div:not(.eltd-sticky-header):not(.fixed) {

    .eltd-main-menu > ul > li {

        > a {
            color: $header-light-color;

            &:hover {
                color: $header-light-hover-color;
            }
        }

        &.eltd-active-item > a {
            color: $header-light-hover-color;
        }
    }
}

.eltd-dark-header .eltd-page-header > div:not(.eltd-sticky-header):not(.fixed) {

    .eltd-main-menu > ul > li {

        > a {
            color: $header-dark-color;

            &:hover {
                color: $header-dark-color;
            }
        }

        &.eltd-active-item > a {
            color: $header-dark-color;
        }
    }
}

/* Narrow drop down styles
   ========================================================================== */
.eltd-drop-down {

    .second {
        position: absolute;
        display: block;
        left: 0;
        top: 100%;
        visibility: hidden;
        overflow: hidden;
        opacity: 0;
        z-index: 10;
        @include eltdTransition(top 0.15s cubic-bezier(0.09, 0.13, 0.13, 0.98));

        &.eltd-drop-down-start {
            visibility: visible;
            overflow: visible;
            opacity: 1;
            @include eltdTransition(opacity 0.25s cubic-bezier(0.09, 0.13, 0.13, 0.98));
        }

        &.right {
            right: 0;
            left: auto;
        }
    
        .inner {
            position: relative;
            display: block;
            padding: 0;
            margin: 0;
            z-index: 997;
            border-top: 1px solid #d0d0d0;
    
            ul {
        
                li {
                    display: block;
                    float: none;
                    height: auto;
            
                    .widget {
                        width: 100%;
                        margin: 0 !important;
                    }
            
                    a {
                        display: block;
                        float: none;
                        margin: 0;
                        padding: 7px 24px;
                
                        .item_outer {
                            position: relative;
                            display: block;
                            overflow: hidden;
                            padding: 0 30px 0 0;
                            box-sizing: border-box;
                            @include eltdTransition(padding .25s cubic-bezier(0.09, 0.13, 0.13, 0.98));

                            &:before {
                                content: '';
                                display: block;
                                position: absolute;
                                left: 0;
                                top: 50%;
                                width: 1px;
                                height: 0;
                                background-color: $default-heading-color;
                                opacity: 0;
                                @include eltdTransition(opacity .25s cubic-bezier(0.09, 0.13, 0.13, 0.98), height .25s cubic-bezier(0.09, 0.13, 0.13, 0.98), top .25s cubic-bezier(0.09, 0.13, 0.13, 0.98));
                            }
                        }
                
                        &:hover {
                    
                            .item_outer {
                                padding: 0 20px 0 10px;
                        
                                &:before {
                                    opacity: 1;
                                    top: 10%;
                                    height: 80%;
                                }
                            }
                        }
                    }
            
                    &.current-menu-ancestor > a,
                    &.current-menu-item > a {
                
                        .item_outer {
                            padding: 0 20px 0 10px;
                    
                            &:before {
                                left: 0;
                                opacity: 1;
                                top: 10%;
                                height: 80%;
                            }
                        }
                    }
            
                    &.sub {
                
                        > a {
                    
                            .item_outer {
                        
                                &:after {
                                    position: absolute;
                                    display: inline-block;
                                    vertical-align: middle;
                                    color: $default-heading-color;
                                    top: 1px;
                                    right: -4px;
                                    font-family: "linea-arrows-10" !important;
                                    content: "\e04b";
                                    font-size: 20px;
                                    font-weight: normal;
                                    font-style: normal;
                                    text-rendering: auto;
                                    -webkit-font-smoothing: antialiased;
                                    -moz-osx-font-smoothing: grayscale;
                                }
                            }
                        }
                    }
                }
        
                &.right {
            
                    li {
                
                        a {
                    
                            .item_outer {
                                padding: 0 0 0 30px;

                                &:before {
                                    left: auto;
                                    right: 0;
                                }
                            }
                    
                            &:hover {
                        
                                .item_outer {
                                    padding: 0 20px 0 10px;
                                }
                            }
                        }
                
                        &.current-menu-ancestor > a,
                        &.current-menu-item > a {
                    
                            .item_outer {
                                padding: 0 10px 0 20px;

                                &:before {
                                    opacity: 1;
                                }

                            }
                        }
                
                        &.sub {
                    
                            > a {
                        
                                .item_outer {
                            
                                    &:after {
                                        right: auto;
                                        left: -2px;
                                        content: "\e03f";
                                    }
                                }
                            }
                        }
                
                        a {
                            text-align: right;
                        }
                
                        ul {
                            left: -100%;
                        }
                    }
                }
            }
        }
    }


    li {
        
        &.left_position {
            
            .second {
                left: 0;
            }
        }
        
        &.right_position {
            
            .second {
                left: auto;
                right: 0;
            }
        }
    }

    > ul > li {
        &:hover > .second {
            z-index: 20;
        }
    }

    .narrow .second .inner {

        ul {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            background-color: #fff;
            padding: 24px 0;
            box-shadow: 4px 2px 8px 0px rgba(0,0,0,0.06);

            li {
                width: 220px;

                ul {
                    position: absolute;
                    left: 100%;
                    top: -25px;
                    height: auto;
                    opacity: 0;
                    overflow: hidden;
                    z-index: 10;
                    border-top: 1px solid #d0d0d0;
                    -webkit-transform: translateZ(0);
                    @include eltdTransition(opacity .4s cubic-bezier(0.09, 0.13, 0.13, 0.98));
                }

                &:hover ul {
                    z-index: 20;
                    opacity: 1;
                }
            }

            &.right li ul {
                left: -100%;
            }
        }
    }
}

/* Wide drop down styles
   ========================================================================== */
.eltd-drop-down .wide {

    .second {

        .inner {
            background-color: #fff;
            text-align: center;
            box-shadow: 4px 2px 8px 0px rgba(0,0,0,0.06);

            ul {

                li {

                    a {
                        padding: 7px 0;
                    }

                    ul {
                        position: relative;
                        display: block;
                        top: 0;
                        left: 0;
                        visibility: visible;

                        li {

                            &:last-child {

                                a {
                                    padding-bottom: 0;
                                }
                            }
                        }
                    }
                }
            }

            > ul {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                width: 1100px;
                padding: 32px 0;
                box-sizing: border-box;

                /*
                @media only screen and (min-width: 1400px) {
                    .eltd-grid-1300 & {
                        width: $grid-width-1300;
                    }
                }

                @media only screen and (min-width: 1300px) {
                    .eltd-grid-1200 & {
                        width: $grid-width-1200;
                    }
                }
                */

                @media only screen and (min-width: 1200px) {
                    .eltd-grid-1000 & {
                        width: $grid-width-1000;
                    }
                }

                @media only screen and (min-width: 1024px) {
                    .eltd-grid-800 & {
                        width: $grid-width-800;
                    }
                }

                > li {
                    float: left;
                    width: 25%;
                    margin: 0;
                    padding: 0 26px;
                    box-sizing: border-box;

                    &:nth-child(4n+1) {
                        clear: both;
                    }

                    > a {
                        padding: 0 0 9px;
                        font-family: $heading-text-font;
                        color: $default-text-color;
                        font-size: 15px;
                        line-height: 1.1em;

                        &.no_link {
                            cursor: default;
                        }

                        .item_outer {
                            padding: 0;
                            overflow: inherit;

                            &:before,
                            &:after {
                                display: none;
                            }
                        }
                    }

                    &.current-menu-ancestor > a,
                    &.current-menu-item > a {
                        opacity:0.85;
                    }
                }
            }
        }
    }

    &.left_position,
    &.right_position {

        .second .inner > ul {
            width: auto;
            white-space: nowrap;

            > li {
                float: none;
                display: inline-block;
                vertical-align: top;

                &:nth-child(4n+1) {
                    border-left: none;
                }
            }
        }

        .second ul li {
            width: 200px;
        }
    }
}
/* ==========================================================================
   Header main menu style - end
   ========================================================================== */