/* ==========================================================================
   Header Standard style - begin
   ========================================================================== */

.eltd-header-standard {
	
	.eltd-menu-area {
		
		&.eltd-menu-left {
			
			.eltd-main-menu {
				width: auto;
				padding: 0 0 0 20px;
			}

			.eltd-vertical-align-containers {

				.eltd-position-left-inner {
					height: 100%;
				}

                .eltd-position-right-inner {
                    height: 100%;
                }
			}
		}
		
		&.eltd-menu-center {

            .eltd-logo-wrapper {
                display: block;
            }

			.eltd-vertical-align-containers {

				.eltd-position-center-inner {
					height: 100%;
				}
			}
		}
		
		&.eltd-menu-right {

            .eltd-logo-wrapper {
                display: block;
            }
			
			.eltd-main-menu {
				width: auto;
			}
			
			.eltd-vertical-align-containers {

				.eltd-position-right-inner {
					height: 100%;
				}
			}
		}
	}



}
/* ==========================================================================
   Header Standard style - end
   ========================================================================== */