/* ==========================================================================
   Sticky header style - begin
   ========================================================================== */

$sticky-header-height: 87px;

.eltd-sticky-header {
	position: fixed;
	top: 0;
	left: 0;
	height: $sticky-header-height;
	width: 100%;
    z-index: 100;
	-webkit-backface-visibility: hidden;
	@include eltdTransform(translateY(-100%));
	@include eltdTransitionTransform(.6s ease);
    
    .eltd-grid {
        height: 100% !important; //override settings for other instances of this div
    }
    
    &.header-appear {
        transform: translateY(0%);
    }
    
    .eltd-sticky-holder {
	    position: relative;
	    height: 100%;
        z-index: 2;
        background-color: #fff;
        border-bottom: 1px solid #d0d0d0;
        box-sizing: border-box;
        
        .eltd-vertical-align-containers {

            padding: 0 40px;

            body:not(.eltd-header-centered) & .eltd-position-left-inner {
                height: 100%; /* cuz of main menu vertical position */
            }
        }

        .in-grid .eltd-vertical-align-containers {
            padding: 0;
        }
	
	    .eltd-logo-wrapper a {
		    max-height: $sticky-header-height;
	    }
    }

    .eltd-position-left .eltd-main-menu {
        padding: 0 0 0 20px;
    }

    .admin-bar &.header-appear {
        top: 32px;
    }
	
	.eltd-sticky-right {
		display: inline-block;
		vertical-align: middle;
	}
	
	.widget {
		display: inline-block;
		vertical-align: middle;
		margin: 0;
	}
}
/* ==========================================================================
   Sticky header style - end
   ========================================================================== */