/* ==========================================================================
   Header Vertical navigation style - begin
   ========================================================================== */

.eltd-header-vertical {

    .eltd-vertical-alignment-center {
    
        .eltd-vertical-menu-outer {
            @include eltdAbsoluteHolderLayout();
            padding: 100px 0;
        }
    
        .eltd-vertical-menu {
            display: table;
            width: 100%;
            height: 100%;
            min-height: 0;
        
            > ul {
                display: table-cell;
                width: 100%;
                height: 100%;
                vertical-align: middle;
            }
        }
    }
    
    .eltd-vertical-menu-outer {
        @include eltdRelativeHolderLayout();
	    box-sizing: border-box;
    }

    .eltd-vertical-menu {
        @include eltdRelativeHolderLayout();
        margin: 75px 0 55px;
        padding: 0;
        z-index: 100;
        
        ul {
            position: relative;
            list-style-type: none;
            padding: 0;
            margin: 0;
            
            li {
                position: relative;
                padding: 0;
                margin: 0;
                
                a {
	                position: relative;
	                display: block;
	                margin: 0;
	                padding: 2px 0;
                    color: $default-text-color;
                    font-size: 14px;
                    
                    &:hover {
                        .eltd-menu-arrow {
                            @include eltdTransform(rotate(0deg));
                        }
                    }
                    
                    .item_outer {
                        position: relative;
                        display: block;
                        padding: 0 15px 0 0;
                        box-sizing: border-box;
                    }
                    
                    .eltd-menu-arrow {
                        position: absolute;
                        top: 1px;
                        right: 0;
                        font-size: 20px;
	                    line-height: 1em;
                        margin: 0;
                        @include eltdTransform(rotate(0deg));
                        @include eltdTransitionTransform(.2s ease-in-out);
                    }
                    
                    .eltd-menu-featured-icon {
                        position: relative;
                        top: -4px;
                        left: 3px;
                        color: $default-heading-color;
                        font-size: 12px;
                        margin: 0;
                    }
                    
                    .menu_icon:not(.blank):not(.null) {
                        padding: 0 5px 0 0;
                    }

                }
	
                &.open {

                    .item_outer {

                        .eltd-menu-arrow {
                            @include eltdTransform(rotate(90deg));
                        }

                    }
                }
                
                ul {
                    padding: 3px 0;
                    
                    li {
                        
                        &.sub {
                            
                            > a {
                                
                                .item_outer {
                                    position: relative;
                                    display: block;
                                    
                                    &:after {
                                        position: absolute;
                                        top: -1px;
                                        right: 0;
                                        margin: 0;
                                        font-family: 'FontAwesome';
	                                    font-size: inherit;
	                                    line-height: inherit;
                                        content: "\f105";
                                    }
                                }
                            }
                            
                            &.current_page_item > a,
                            &.current-menu-ancestor > a {
                                
                                .item_outer:after {
                                    content: "\f107";
                                }
                            }
                        }
                    }
                }
                
                .second {
                    display: none;
                    @include eltdTransform(translateZ(0));
                    
                    .inner {
                        position: relative;
                        display: block;
                        padding: 0;
                        z-index: 997;

                        a {
                            @include eltdTransition(all 0.25s cubic-bezier(0.09, 0.13, 0.13, 0.98));

                            &:before {
                                content: '';
                                display: block;
                                position: absolute;
                                left: 0;
                                top: 50%;
                                width: 1px;
                                height: 0;
                                background-color: #121212;
                                opacity: 0;
                                @include eltdTransition(opacity 0.25s cubic-bezier(0.09, 0.13, 0.13, 0.98), height 0.25s cubic-bezier(0.09, 0.13, 0.13, 0.98), top 0.25s cubic-bezier(0.09, 0.13, 0.13, 0.98));
                            }

                            &:hover {
                                padding-left:10px;

                                &:before {
                                    opacity: 1;
                                    top: 30%;
                                    height: 40%;
                                }
                            }
                        }
                    }
                }
            }
        }
        
        > ul {
            
            > li {
                
                > a {

                    font-size: 12px;
                    line-height: 30px;
                    font-weight: 400;
                    letter-spacing: 2px;
                    text-transform: uppercase;
                    padding: 10px 0;

                    color: $default-heading-color;

                     span.item_text {
                        position: relative;
                        display: inline-block;
                        vertical-align: top;

                        &:after {
                            content:"";
                            position:absolute;
                            bottom:5px;
                            left:0;
                            width:calc(100% - 2px);
                            height:1px;
                            background-color:#545454;
                            @include eltdTransition(all .2s cubic-bezier(0.32, 0.93, 0.49, 0.77) 0.05s);
                            @include eltdTransform(scale(0,1));
                        }
                    }

                    &.current {
                        span.item_text {
                            position:relative;

                            &:after {
                                @include eltdTransform(scale(1,1));
                            }
                        }
                    }

                    &:hover {
                        span.item_text { 
                            &:after {
                                @include eltdTransform(scale(1,1));
                            }
                        }
                    }

                }
            }
        }
    }
}

.eltd-light-header.eltd-header-vertical .eltd-vertical-menu ul li {
    
    a {
        color: $header-light-color !important;
        
        &:hover {
            color: $header-light-hover-color !important;
        }
    }
    
    ul li {
        
        &.current_page_item > a,
        &.current-menu-item > a,
        &.current-menu-ancestor > a {
            color: $header-light-hover-color !important;
        }
    }
}

.eltd-light-header.eltd-header-vertical .eltd-vertical-menu > ul > li {
    
    &.eltd-active-item > a,
    &.current-menu-ancestor > a {
        color: $header-light-hover-color !important;
    }
}

.eltd-dark-header.eltd-header-vertical .eltd-vertical-menu ul li {
    
    a {
        color: $header-dark-color !important;
        
        &:hover {
            color: $header-dark-hover-color !important;
        }
    }
    
    ul li {
        
        &.current_page_item > a,
        &.current-menu-item > a,
        &.current-menu-ancestor > a {
            color: $header-dark-hover-color !important;
        }
    }
}

.eltd-dark-header.eltd-header-vertical .eltd-vertical-menu > ul > li {
    
    &.eltd-active-item > a,
    &.current-menu-ancestor > a {
        color: $header-dark-hover-color !important;
    }
}
/* ==========================================================================
   Header Vertical navigation style - end
   ========================================================================== */